@import "mixin";

.fixed {
	position: fixed;
	top: -5px;
	left: 0;
	width: 100%;
	padding: 10px 0; }

.header {
	width: 100%; }
.st-container {
	width: $width;
	// min-width: 1000px
	max-width: 95%;
	margin: 0 auto;
	padding: 30px 0;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between; }
.logo {
	font-family: "Raleway";
	font-size: 28px;
	position: relative;
	top: 5px;
	div {
		color: $white;
		margin-left: 10px;
		display: inline-block;
		position: relative;
		top: -55%;
		transform: translateY(50%);
		font-family: "Raleway"; } }
.ninja {
	display: none; }

nav {
	width: 65%;
	text-align: right;
	li {
		display: inline-block;
		// position: relative
		// top: 50%
		// transform: translateY(-50%)
		margin-left: 3.5%;
		a {
			text-transform: uppercase;
			text-decoration: none;
			color: $white;
			font-family: "FuturaPT";
			font-size: 14px;
			padding: 8px 0; }
		a:hover {
			border-bottom: 2px solid $green;
			color: $green; } }
	li:first-child {
		margin-left: 0px; }
	.logo {
		display: none; } }

.fixed {
	position: fixed;
	top: 130px;
	left: 0;
	right: 0;
	width: 110%;
	padding: 10px 0;
	margin: 0;
	// height: 70px
	background: red;
	// display: block
	z-index: 500; }


.headhesive {
	z-index: 2000;
	background: $white;
	// width: 95%
	margin: auto;
	position: fixed;
	left: 0;
	right: 0;
	transform: translateY(-100%);
	transition: 0.4s;
	.st-container {
		padding: 0; }

	.logo {
		// display: block
		div {
			color: #000; } }
	li {
		// top: 0
		// transform: translateY(0)
		a {
			color: #000; } } }

.headhesive--stick {
	transform: translateY(50px); }


