@media screen and (max-width: 720px) {

	.fixed, .headhesive {
		display: none; }
	.bunner {
		.ninja {
			width: 20px;
			height: 20px;
			background: url("../image/Menu.png") center center no-repeat;
			position: absolute;
			left: 15px;
			top: 30px;
			border: none; }
		.ninja:active {}
		.ninja:focus {
			outline: none; }

		nav {
			@include transition (0.4s);
			position: absolute;
			top: 0;
			left: -200%;
			background: grey;
			height: 3538px;
			width: 250px;
			@include box-shadow(130px 0px 0px 45px rgba(0,0,0,0.59));
			li {
				display: block;
				text-align: left;
				font-size: 16px;
				margin-left: 15px;
				margin-bottom: 35px;
				top: 0%;
				transform: translateY(0%); }
			li:before {
				content: '\2022';
				color: $white;
				margin-right: 15px; }
			.logo {
				display: block;
				text-align: center;
				margin-top: 20px;
				margin-bottom: 40px; } }
		.ninja:focus ~  nav,
		.ninja:active ~  nav {
			left: 0%;
			z-index: 200; }
		nav:hover li {
			display: block; } }
	.bunner {
		width: $w375;
		margin: auto;
		background: url('../image/1-Home.png')  center top no-repeat;
		.st-container {
			padding: 20px 0; }
		.logo {
			margin: auto;

			div {
				transform: translateY(-55%);
				font-size: 18px; } } }

	.ninja {
		display: block; }

